import React from 'react';

// 1. 구조분해로 props를 정의하면 속성값이 전달되지 않는 경우를 대비하여 기본값을 설정 가능
// 2. 시작태그와 끝태그 사이로 전달되는 내용을 children이라 함
//    --> props 객체에 항상 맨 마지막 속성으로 전달됨
//        이름이 항상 고정되어 있다.
const MyChildrenSub = ({color, size=3, children}) => {
    return (
        <div>
            <h3>MyChildrenSub</h3>
            <font color={color} size={size}>{children}</font>
        </div>
    );
};

export default MyChildrenSub;